import styled, { css } from 'styled-components';
import theme from '../theme';

export const StyledButtonComponent = styled.button`
  display: inline-block;
  font-size: 1.4rem;
  font-weight: 600;
  width: max-content;
  border: 2px solid;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin-left: 2%;
  cursor: pointer;

  /* Default Styles */
  background-color: ${({ outline, type }) =>
    outline ? 'transparent' : theme?.[type]};
  color: ${({ outline, type }) => (outline ? theme?.[type] : 'white')};

  ${({ outline, disableHover, type }) => css`
    border-color: ${outline ? 'transparent' : theme?.[type]};

    ${!disableHover &&
    css`
      &:hover,
      &:active {
        background-color: ${outline ? theme?.[type] : theme?.[type]};
        color: white;
      }
    `}
  `}
`;
